/* eslint-disable max-statements */
import React, { useState, useRef, useMemo, useEffect } from 'react';
import { useVirtual } from 'react-virtual';
import { v4 as uuidv4 } from 'uuid';

import { TreeViewPropTypes } from './prop-types';
import TreeViewContext, {
  defaultProps,
  defaultRelativeMouseCoord,
} from './TreeViewContext';
import { TreeDnD, TreeDndPlugin } from './plugins/dnd';
import { useTree } from './utils/useTree';
import { TreeList } from './parts/TreeList';
import { useNotifyExpandedChange } from './utils/group-expands-helpers';
import { useInstanceRefActions } from './utils/useInstanceRefActions';
import { useGlobalToggleAllExpandShortcut } from './utils/keyboard-helpers';
export function TreeView(props) {
  const {
    data,
    plugins,
    onOrderChange,
    isMultiSelect,
    isSingleSelect,
    instanceRef,
    selection,
  } = props;
  const [isDragging, setIsDragging] = useState(false);
  const [isKeyboardDragging, setIsKeyboardDragging] = useState(false);
  const [draggedItem, setDraggedItem] = useState(null);
  const [hoverItem, setHoverItem] = useState(null);
  const [relativeMouseCord, setRelativeMouseCord] = useState(
    defaultRelativeMouseCoord,
  );
  const [uniqueTreeViewUUID] = useState(uuidv4());
  const [latestToggledItem, setLatestToggledItem] = useState(null);
  const [selectedItem, setSelectedItem] = useState(null);
  const [focusedItem, setFocusedItem] = useState(null);
  const [selectedCheckboxes, setSelectedCheckboxes] = useState({
    ...selection,
  });
  const [expandedGroups, setExpandedGroups] = useState({});

  const propsWithDefaults = useMemo(() => ({ ...defaultProps, ...props }), [
    props,
  ]);

  const { visibleItems, tree, treeRoot, triggerTreeRerender } = useTree(
    data,
    propsWithDefaults,
    {
      setExpandedGroups,
    },
  );

  const virtualListRef = useRef();
  // estimateSize should not be really required given what was stated on
  // https://github.com/tannerlinsley/react-virtual/issues/23

  const virtualListHelpers = useVirtual({
    size: visibleItems.length,
    parentRef: virtualListRef,
    overscan: 15,
  });

  const withDragAndDrop = plugins?.includes(TreeDndPlugin) && onOrderChange;
  const withRadioChecks = isSingleSelect && !isMultiSelect;
  const withCheckboxChecks = !isSingleSelect && isMultiSelect;

  const ctx = {
    props: { ...propsWithDefaults },
    setRelativeMouseCord,
    relativeMouseCord,
    virtualListHelpers,
    virtualListRef,
    tree,
    treeRoot,
    visibleItems,
    triggerTreeRerender,
    isDragging,
    setIsDragging,
    isKeyboardDragging,
    setIsKeyboardDragging,
    draggedItem,
    setDraggedItem,
    withRadioChecks,
    withCheckboxChecks,
    withDragAndDrop,
    uniqueTreeViewUUID,
    selectedCheckboxes,
    setSelectedCheckboxes,
    selectedItem,
    setSelectedItem,
    expandedGroups,
    setExpandedGroups,
    latestToggledItem,
    setLatestToggledItem,
    focusedItem,
    setFocusedItem,
    hoverItem,
    setHoverItem,
  };
  // this hooks can't use the context because the context provider doesn't wrap this
  // as such we manually pass the second parameter as fit
  useInstanceRefActions(instanceRef, ctx);
  useGlobalToggleAllExpandShortcut(
    treeRoot,
    triggerTreeRerender,
    setLatestToggledItem,
  );
  useNotifyExpandedChange(propsWithDefaults, ctx);

  useEffect(() => {
    ctx.props.onInstanceRefInitialized(instanceRef);
  }, []);

  return (
    <TreeViewContext.Provider value={ctx}>
      {plugins?.includes(TreeDndPlugin) ? <TreeDnD /> : <TreeList />}
    </TreeViewContext.Provider>
  );
}

TreeView.propTypes = TreeViewPropTypes;
