/* eslint-disable max-lines */
/* eslint-disable max-statements */
/* eslint-disable complexity */
import { useCallback, useContext, useEffect } from 'react';
import TreeViewContext from '../TreeViewContext';
import { focusItem } from './tree-helpers';
import {
  toggleExpandAllHelper,
  toggleItemExpand,
} from './group-expands-helpers';

// 9 tab
// 13 enter
// 32 space
// 37 left arrow
// 38 up arrow
// 39 right arrow
// 40 down arrow
// 35 end
// 36 home

// 56 number 8
// 106 (NumPad) *

export const useOnItemKeyDown = (item) => {
  const ctx = useContext(TreeViewContext);
  const {
    props: { onItemFocus },
    triggerTreeRerender,
    isDragging,
    visibleItems,
    setFocusedItem,
    setSelectedItem,
    setLatestToggledItem,
    virtualListHelpers: { scrollToIndex },
  } = ctx;
  const { isGroup, isExpanded } = item;
  const onItemFocusCurry = useCallback(
    (newFocusItem) =>
      onItemFocus({
        itemIndex: newFocusItem.virtualIndex,
        scrollToItem: (opts = { align: 'start' }) =>
          scrollToIndex(newFocusItem.virtualIndex, opts),
        item: newFocusItem,
      }),
    [onItemFocus],
  );

  const onItemKeyDown = useCallback(
    (e) => {
      const { keyCode } = e;
      if (!isDragging) {
        if (keyCode !== 9) e.preventDefault(); // allow tab/shift+tab
        if (keyCode === 13 || keyCode === 32) setSelectedItem(item);

        if (keyCode === 37) {
          if (isGroup && isExpanded) {
            toggleItemExpand(item, triggerTreeRerender, scrollToIndex);
            setLatestToggledItem(item);
          }
        }
        if (keyCode === 39) {
          if (isGroup && !isExpanded) {
            toggleItemExpand(item, triggerTreeRerender, scrollToIndex);
            setLatestToggledItem(item);
          }
        }

        const { virtualIndex } = item;
        const lastItemIndex = visibleItems.length - 1;
        if (keyCode === 38) {
          const isLoopedFocus = virtualIndex === 0;
          const newFocusIndex = isLoopedFocus
            ? lastItemIndex
            : virtualIndex - 1;
          const newFocusItem = visibleItems[newFocusIndex];
          scrollToIndex(newFocusIndex);
          if (isLoopedFocus) {
            // when looping the list from start to end we need to wait a bit before focusing
            setTimeout(() => {
              setFocusedItem(newFocusItem);
              focusItem(newFocusItem);
              onItemFocusCurry(newFocusItem);
            }, 300);
          } else {
            setFocusedItem(newFocusItem);
            onItemFocusCurry(newFocusItem);
            focusItem(newFocusItem);
          }
        }
        if (keyCode === 40) {
          const isLoopedFocus = virtualIndex === lastItemIndex;
          const newFocusIndex = isLoopedFocus ? 0 : virtualIndex + 1;
          const newFocusItem = visibleItems[newFocusIndex];
          scrollToIndex(newFocusIndex);
          if (isLoopedFocus) {
            // when looping the list from start to end we need to wait a bit before focusing
            setTimeout(() => {
              setFocusedItem(newFocusItem);
              onItemFocusCurry(newFocusItem);
              focusItem(newFocusItem);
            }, 300);
          } else {
            setFocusedItem(newFocusItem);
            onItemFocusCurry(newFocusItem);
            focusItem(newFocusItem);
          }
        }
        if (keyCode === 35) {
          if (virtualIndex !== lastItemIndex) {
            const newFocusItem = visibleItems[lastItemIndex];
            scrollToIndex(lastItemIndex);
            setTimeout(() => {
              setFocusedItem(newFocusItem);
              onItemFocusCurry(newFocusItem);
              focusItem(newFocusItem);
            }, 300);
          }
        }
        if (keyCode === 36) {
          if (virtualIndex !== 0) {
            const newFocusItem = visibleItems[0];
            scrollToIndex(0);
            setTimeout(() => {
              setFocusedItem(newFocusItem);
              onItemFocusCurry(newFocusItem);
              focusItem(newFocusItem);
            }, 300);
          }
        }
      }
    },
    [
      item,
      visibleItems,
      isGroup,
      isDragging,
      onItemFocusCurry,
      setFocusedItem,
      setSelectedItem,
    ],
  );

  return onItemKeyDown;
};

export const useGlobalKeyboardListener = (func) => {
  useEffect(() => {
    document.addEventListener('keydown', func);
    return () => document.removeEventListener('keydown', func);
  }, [func]);
};

export const useGlobalToggleAllExpandShortcut = (
  treeRoot,
  triggerTreeRerender,
  setLatestToggledItem,
) => {
  const memoizedToggleAllKeyboardShortcut = useCallback(
    (e) => {
      const { shiftKey, keyCode } = e;
      if (shiftKey && keyCode === 56) {
        toggleExpandAllHelper(
          'toggle',
          treeRoot,
          triggerTreeRerender,
          setLatestToggledItem,
        );
      }
    },
    [treeRoot, triggerTreeRerender],
  );

  return useGlobalKeyboardListener(memoizedToggleAllKeyboardShortcut);
};
