import React, { useContext, useCallback } from 'react';
import Button from '@elliemae/ds-basic/Button';
import ArrowheadDown from '@elliemae/ds-icons/ArrowheadDown';
import ArrowheadRight from '@elliemae/ds-icons/ArrowheadRight';
import { toggleItemExpand } from '../utils/group-expands-helpers';
import { focusItem } from '../utils/tree-helpers';
import TreeViewContext from '../TreeViewContext';
import { TreeItemExpandCaretPropTypes } from '../prop-types';

export const ExpandCaret = (props) => {
  const defaultHandleExpandGroup = useCallback(toggleItemExpand, []);
  const { item } = props;
  const { id, isExpanded, isGroup, children } = item;
  const isGroupOrHasChildrens =
    isGroup || (Array.isArray(children) && children.length > 0);
  const ctx = useContext(TreeViewContext);
  const {
    props: { onItemFocus },
    handleExpandGroup = defaultHandleExpandGroup,
    triggerTreeRerender,
    setLatestToggledItem,
    setFocusedItem,
    virtualListHelpers: { scrollToIndex },
  } = ctx;

  if (isGroupOrHasChildrens)
    return (
      <Button
        buttonType="text"
        className="expandable-arrow"
        data-testid="tree-item-expand-toggle"
        key={`${id}-expand-addon`}
        icon={
          isExpanded ? (
            <ArrowheadDown size="s" data-testid="ic-arrow-head-down" />
          ) : (
            <ArrowheadRight size="s" data-testid="ic-arrow-head-right" />
          )
        }
        onMouseDown={(e) => {
          // prevent focusing the caret
          if (e) {
            e.preventDefault();
          }
        }}
        onClick={(e) => {
          e.stopPropagation();
          handleExpandGroup(item, triggerTreeRerender, scrollToIndex, e);
          setLatestToggledItem(item);
          onItemFocus({
            itemIndex: item.virtualIndex,
            scrollToItem: (opts = { align: 'start' }) =>
              scrollToIndex(item.virtualIndex, opts),
            item,
          });
          setFocusedItem(item);
          focusItem(item);
        }}
        size="s"
        tabIndex={-1}
      />
    );
  return <div style={{ width: '24px' }} />;
};

ExpandCaret.propTypes = TreeItemExpandCaretPropTypes;
