/* eslint-disable max-lines */
import React, { useMemo, useContext } from 'react';
import PropTypes from 'prop-types';
import Highlighter from 'react-highlight-words';
import DSTruncatedTooltipText, {
  TooltipTextProvider,
} from '@elliemae/ds-basic/TruncatedTooltipText';
import { aggregatedClasses } from '@elliemae/ds-classnames';

import { ChildrenCountDisplayer } from './ChildrenCountDisplayer';
import {
  DSTreeViewPrefix,
  treeItemBlockName,
} from '../config/cssClassesConstants';

import TreeViewContext from '../TreeViewContext';

const ItemContent = aggregatedClasses('div')(`${treeItemBlockName}-content`);
const ItemValue = aggregatedClasses('span')(treeItemBlockName, 'value');

// const wrapperClassName = `${DSTreeViewPrefix}-${treeItemBlockName}-content`;
const classNameModifierWrapAll = `${DSTreeViewPrefix}-${treeItemBlockName}__value--wrap-all`;
const classNameModifierTruncate = `${DSTreeViewPrefix}-${treeItemBlockName}__value--truncate`;
export function TreeItemText(props) {
  const { item } = props;

  const ctx = useContext(TreeViewContext);
  const {
    props: { searchQuery, showChildrenAmount, labelOverflow, nameKey },
  } = ctx;

  const Text = useMemo(() => {
    const itemLabelText = item[nameKey];
    if (searchQuery) {
      if (labelOverflow === 'truncate') {
        return (
          <TooltipTextProvider>
            <DSTruncatedTooltipText
              value={
                <>
                  <Highlighter
                    autoEscape
                    highlightClassName="tree-text-highlight"
                    searchWords={[searchQuery]}
                    textToHighlight={itemLabelText}
                  />
                  {showChildrenAmount ? (
                    <ChildrenCountDisplayer item={item} />
                  ) : null}
                </>
              }
            />
          </TooltipTextProvider>
        );
      }
      return (
        <>
          <Highlighter
            autoEscape
            highlightClassName="tree-text-highlight"
            searchWords={[searchQuery]}
            textToHighlight={itemLabelText}
          />
          {showChildrenAmount ? <ChildrenCountDisplayer item={item} /> : null}
        </>
      );
    }
    if (labelOverflow === 'truncate') {
      return (
        <TooltipTextProvider>
          <DSTruncatedTooltipText
            value={
              <>
                {itemLabelText}
                {showChildrenAmount ? (
                  <ChildrenCountDisplayer item={item} />
                ) : null}
              </>
            }
          />
        </TooltipTextProvider>
      );
    }
    return (
      <>
        {itemLabelText}
        {showChildrenAmount ? <ChildrenCountDisplayer item={item} /> : null}
      </>
    );
  }, [item, searchQuery, nameKey, showChildrenAmount, labelOverflow]);

  let className = '';
  if (labelOverflow === 'wrap-all') className += `${classNameModifierWrapAll} `;
  if (labelOverflow === 'truncate')
    className += `${classNameModifierTruncate} `;

  return (
    <ItemContent data-testid={`${treeItemBlockName}-content`}>
      <ItemValue className={className}>{Text}</ItemValue>
    </ItemContent>
  );
}

TreeItemText.propTypes = {
  item: PropTypes.object.isRequired,
};
