/* eslint-disable max-lines */
import React, {
  useContext,
  useState,
  useMemo,
  useRef,
  useCallback,
} from 'react';
import { SortableElement, SortableHandle } from '@elliemae/react-sortable-hoc';

import { aggregatedClasses } from '@elliemae/ds-classnames';
import GripperVertical from '@elliemae/ds-icons/GripperVertical';

import { TreeItemPropTypes } from '../prop-types';

import RadioSelectable from './RadioSelectable';
import CheckboxSelectable from './CheckboxSelectable';
import TreeViewContext from '../TreeViewContext';
import { setMultipleRefs } from '../utils/refs-helpers';
import { useOnItemKeyDown } from '../utils/keyboard-helpers';
import { NestingSpace } from './NestingSpace';
import { Icon } from './Icon';
import { ExpandCaret } from './ExpandCaret';
import { TreeItemText } from './TreeItemText';
import {
  DSTreeViewPrefix,
  treeItemBlockName,
} from '../config/cssClassesConstants';

export const TreeListItem = aggregatedClasses('li', {
  'data-testid': treeItemBlockName,
  role: 'treeitem',
})(treeItemBlockName);
export const TreeListItemAddonsWrapper = aggregatedClasses('div')(
  treeItemBlockName,
  'addons',
);
const DnDHandle = SortableHandle(({ id }) => (
  <GripperVertical
    data-testid="drag-handle"
    key={`${id}-drag-handle`}
    className="drag-handle"
    size="s"
    onMouseDown={(e) => {
      // prevent focusing the drag-handle to avoid borders
      if (e) {
        e.preventDefault();
      }
    }}
    tabIndex="0"
  />
));

export const TreeItem = (props) => {
  const {
    item,
    itemIndex,
    itemWrapperStyle = {},
    virtualItemRef,
    spaceForNestingLevel = 1.5,
    onMouseDragOverItem,
  } = props;
  const { id, node } = item;
  const { nodeItemRef } = node.model;
  const openFolderOnHoverTimeout = useRef();

  const [isDraggingOverThis, setIsDraggingOverThis] = useState(false);
  const onItemKeyDown = useOnItemKeyDown(item);

  const ctx = useContext(TreeViewContext);
  const {
    props: {
      disableIcons,
      rightAddons,
      onItemClick,
      onItemFocus,
      rowSize,
      labelOverflow,
    },
    relativeMouseCord,
    withRadioChecks,
    withCheckboxChecks,
    withDragAndDrop,
    selectedItem,
    selectedCheckboxes,
    hoverItem,
    isDragging,
    virtualListHelpers: { scrollToIndex },
    setHoverItem,
    setSelectedItem,
    setFocusedItem,
  } = ctx;

  // Sortable Items needs to update
  //   - the virtual list ref (for react-virtual requirement)
  //   - nodeItemRef which is a prop avaiable in the TreeModel nodes
  const setItemRefs = (ref) => {
    setMultipleRefs(virtualItemRef, nodeItemRef)(ref);
  };

  const dropPreviewClassName = useMemo(() => {
    if (!withDragAndDrop || !relativeMouseCord || !isDraggingOverThis)
      return '';
    const { isBefore, isAfter, isValid } = relativeMouseCord;
    let className = `${DSTreeViewPrefix}-${treeItemBlockName}${
      isValid ? '--valid' : '--invalid'
    }`;

    if (isBefore) className += '-drop-before';
    else if (isAfter) className += '-drop-after';
    else className += '-drop-in';
    return className;
  }, [withDragAndDrop, relativeMouseCord, isDraggingOverThis]);

  const selectedClassName = useMemo(
    () => (selectedItem?.id === item.id ? 'selected' : ''),
    [selectedItem],
  );
  const hoverClassName = useMemo(
    () =>
      hoverItem?.id !== item.id || (withDragAndDrop && isDragging)
        ? ''
        : 'hover',
    [hoverItem, withDragAndDrop, isDragging],
  );
  const fixedHeightClassname = useMemo(() => {
    if (labelOverflow === 'truncate') {
      if (rowSize === 'compact')
        return `${DSTreeViewPrefix}-${treeItemBlockName}--fixed-height-compact`;
      return `${DSTreeViewPrefix}-${treeItemBlockName}--fixed-height`;
    }
    return '';
  }, [labelOverflow, rowSize]);

  const finalClassName = useMemo(
    () =>
      `${dropPreviewClassName} ${fixedHeightClassname} ${selectedClassName} ${hoverClassName}`,
    [
      dropPreviewClassName,
      selectedClassName,
      hoverClassName,
      fixedHeightClassname,
    ],
  );

  const handleItemClick = useCallback(
    (e) => {
      onItemClick(item, e);
      setFocusedItem(item);
      setSelectedItem(item);
      onItemFocus({
        itemIndex: item.virtualIndex,
        scrollToItem: (opts = { align: 'start' }) =>
          scrollToIndex(item.virtualIndex, opts),
        item,
      });
    },
    [
      item,
      scrollToIndex,
      onItemClick,
      onItemFocus,
      setSelectedItem,
      setFocusedItem,
    ],
  );

  return (
    <TreeListItem
      tabIndex="0"
      onMouseMove={(event) => {
        if (withDragAndDrop) {
          onMouseDragOverItem({
            event,
            item,
            itemIndex,
            setIsDraggingOverThis,
            openFolderOnHoverTimeout,
          });
        }
      }}
      onMouseLeave={() => {
        if (withDragAndDrop) {
          setIsDraggingOverThis(false);
          if (openFolderOnHoverTimeout?.current)
            clearTimeout(openFolderOnHoverTimeout.current);
        }
        if (hoverItem?.id === item.id) {
          setHoverItem(null);
        }
      }}
      onMouseEnter={() => {
        setHoverItem(item);
      }}
      onClick={handleItemClick}
      ref={setItemRefs}
      style={{ ...itemWrapperStyle }}
      className={finalClassName}
      aria-expanded={item.isExpanded}
      aria-selected={selectedCheckboxes[item.id]}
      data-id={item.id}
      data-index={item.virtualIndex}
      onKeyDown={onItemKeyDown}
    >
      <TreeListItemAddonsWrapper>
        {withDragAndDrop ? <DnDHandle id={id} /> : null}
        {withRadioChecks ? (
          <RadioSelectable item={item} itemIndex={itemIndex} />
        ) : null}
        {withCheckboxChecks ? (
          <CheckboxSelectable item={item} itemIndex={itemIndex} />
        ) : null}
        <NestingSpace item={item} spaceForNestingLevel={spaceForNestingLevel} />
        <ExpandCaret item={item} />
        {!disableIcons ? <Icon item={item} /> : null}
      </TreeListItemAddonsWrapper>
      <TreeItemText item={item} />
      {rightAddons && (
        <TreeListItemAddonsWrapper>
          {typeof rightAddons === 'function' ? rightAddons(item) : rightAddons}
        </TreeListItemAddonsWrapper>
      )}
    </TreeListItem>
  );
};

TreeItem.propTypes = TreeItemPropTypes;

export const DragAndDropTreeItem = SortableElement(TreeItem);
