import { useCallback, useMemo, useEffect } from 'react';
import { focusItem } from './tree-helpers';
import { toggleExpandAllHelper } from './group-expands-helpers';

export const useInstanceRefActions = (instanceRef, ctx) => {
  const {
    props: { onItemFocus },
    virtualListHelpers,
    treeRoot,
    visibleItems,
    triggerTreeRerender,
    setSelectedItem,
    setFocusedItem,
    setLatestToggledItem,
  } = ctx;
  const { scrollToIndex } = useMemo(() => virtualListHelpers, [
    virtualListHelpers,
  ]);

  const toggleExpandAll = useCallback(
    (isExpand) => {
      toggleExpandAllHelper(
        isExpand,
        treeRoot,
        triggerTreeRerender,
        setLatestToggledItem,
      );
    },
    [
      toggleExpandAllHelper,
      setLatestToggledItem,
      treeRoot,
      triggerTreeRerender,
    ],
  );
  const scrollTo = useCallback(
    (indx, opts = { align: 'start' }) => {
      if (scrollToIndex) scrollToIndex(indx, opts);
    },
    [scrollToIndex],
  );

  const setSelectedItemByVirtualIndex = useCallback(
    (index) => {
      const itemByIndex = visibleItems[index];
      setSelectedItem(itemByIndex);
    },
    [setSelectedItem, visibleItems],
  );
  const setFocusedItemByVirtualIndex = useCallback(
    (index, opts = { align: 'start', withScroll: false }) => {
      const itemByIndex = visibleItems[index];
      const onItemFocusParams = {
        itemIndex: itemByIndex.virtualIndex,
        scrollToItem: () => scrollToIndex(index, opts),
        item: itemByIndex,
      };
      if (opts.withScroll && scrollToIndex) {
        scrollToIndex(index, opts);
        setTimeout(() => {
          setFocusedItem(itemByIndex);
          focusItem(itemByIndex);
          onItemFocus(onItemFocusParams);
        }, 300);
      } else {
        setFocusedItem(itemByIndex);
        focusItem(itemByIndex);
        onItemFocus(onItemFocusParams);
      }
    },
    [scrollToIndex, setFocusedItem, onItemFocus, visibleItems],
  );

  const actions = useMemo(
    () => ({
      toggleExpandAll,
      scrollTo,
      setSelectedItemByVirtualIndex,
      setFocusedItemByVirtualIndex,
    }),
    [toggleExpandAll, scrollTo, setSelectedItemByVirtualIndex],
  );

  useEffect(() => {
    if (instanceRef && instanceRef.current)
      instanceRef.current.actions = actions;
    if (instanceRef && !instanceRef.current) {
      instanceRef.current = { actions };
    }
  }, [instanceRef, actions]);

  return actions;
};
