import React, { useContext, useCallback, useMemo } from 'react';
import DSCheckbox from '@elliemae/ds-basic/form/Checkbox';
import { TreeItemCheckboxPropTypes } from '../prop-types';
import TreeViewContext from '../TreeViewContext';
import { toggleCheckboxItem } from '../utils/selectable-helper';
import { filterObject } from '../utils/object-helpers';

function CheckboxSelectable(props) {
  const { item, itemIndex } = props;
  const { id } = item;

  const ctx = useContext(TreeViewContext);
  const {
    props: {
      disableGroups, // radio allows disabling this, checkbox doesnt? check specs
      disableLeafs, // radio allows disabling this, checkbox doesnt? check specs
      onSelectionChange,
    },
    virtualListHelpers: { scrollToIndex },
    triggerTreeRerender,
    selectedCheckboxes,
    setSelectedCheckboxes,
  } = ctx;

  const onCheckboxChange = useCallback(() => {
    const newSelections = toggleCheckboxItem(item);
    const newSelectionHasmap = filterObject(
      { ...selectedCheckboxes, ...newSelections },
      (value) => value !== false,
    );
    setSelectedCheckboxes(newSelectionHasmap);
    triggerTreeRerender();

    const indexAndScrollTo = {
      scrollToItem: () => {
        scrollToIndex(item.virtualIndex);
      },
      itemIndex,
    };
    if (onSelectionChange)
      onSelectionChange(newSelectionHasmap, item, indexAndScrollTo);
  }, [
    item,
    triggerTreeRerender,
    onSelectionChange,
    itemIndex,
    scrollToIndex,
    selectedCheckboxes,
    setSelectedCheckboxes,
  ]);
  const isChecked = useMemo(() => {
    if (selectedCheckboxes[id] === 'mixed') {
      return 'mixed';
    }
    if (selectedCheckboxes[id] === true) {
      return true;
    }
    return false;
  }, [selectedCheckboxes, id]);

  return (
    <DSCheckbox
      checked={isChecked}
      disabled={item.isGroup ? disableGroups : disableLeafs}
      className="em-ds-tree-item-checkbox"
      data-testid="tree-item-checkbox"
      onChange={onCheckboxChange}
      tabIndex="0"
    />
  );
}

CheckboxSelectable.propTypes = TreeItemCheckboxPropTypes;

export default CheckboxSelectable;
