import React, { useContext, useCallback, useMemo } from 'react';
import PropTypes from 'prop-types';
import Radio from '@elliemae/ds-basic/form/Radio';
import { TreeViewItemPropType } from '../prop-types';
import TreeViewContext from '../TreeViewContext';

function RadioSelectable(props) {
  const { item, itemIndex } = props;

  const ctx = useContext(TreeViewContext);
  const {
    props: {
      disableGroups, // radio allows disabling this, checkbox doesnt? check specs
      disableLeafs, // radio allows disabling this, checkbox doesnt? check specs
      onSelectionChange,
    },
    virtualListHelpers: { scrollToIndex },
    uniqueTreeViewUUID,
    selectedCheckboxes,
    setSelectedCheckboxes,
  } = ctx;

  const { id } = item;

  const onRadioChange = useCallback(() => {
    const newSelectionHasmap = { [id]: true };
    setSelectedCheckboxes(newSelectionHasmap);
    const indexAndScrollTo = {
      scrollToItem: () => {
        scrollToIndex(item.virtualIndex);
      },
      itemIndex,
    };
    if (onSelectionChange)
      onSelectionChange(newSelectionHasmap, item, indexAndScrollTo);
  }, [item, id, onSelectionChange, scrollToIndex]);

  const radioName = useMemo(() => `radios-${uniqueTreeViewUUID}`, [
    uniqueTreeViewUUID,
  ]);

  return (
    <Radio
      checked={selectedCheckboxes[id] === true}
      name={radioName}
      disabled={item.isGroup ? disableGroups : disableLeafs}
      className="em-ds-tree-item-radio"
      data-testid="tree-item-radio"
      onChange={onRadioChange}
      tabIndex="0"
    />
  );
}

RadioSelectable.propTypes = {
  item: TreeViewItemPropType.isRequired,
  itemIndex: PropTypes.number.isRequired,
};

export default RadioSelectable;
