import React, { useContext } from 'react';

import { DragAndDropTreeList } from '../../parts/TreeList';

import { useDnDHelpers } from '../../utils/useDnDHelpers';
import { keyCodes } from '../../utils/dnd-helpers';

import TreeViewContext from '../../TreeViewContext';
export const TreeDnD = () => {
  const {
    updateBeforeSortStart,
    onSortStart,
    onMouseDragOverItem,
    onSortEnd,
  } = useDnDHelpers();
  const ctx = useContext(TreeViewContext);
  const { virtualListRef } = ctx;
  return (
    <DragAndDropTreeList
      useDragHandle
      transitionDuration={0}
      keyboardSortingTransitionDuration={0}
      hideSortableGhost={false}
      disableAnimation
      updateBeforeSortStart={updateBeforeSortStart}
      onSortStart={onSortStart}
      onMouseDragOverItem={onMouseDragOverItem}
      onSortEnd={onSortEnd}
      getContainer={() => virtualListRef.current}
      keyCodes={keyCodes}
    />
  );
};

export const TreeDndPlugin = 'tree-dnd';
