/* eslint-disable max-params */
import { useCallback, useEffect } from 'react';

export const toggleItemExpand = (
  itemToToggle,
  triggerTreeRerender,
  scrollTo = () => {},
) => {
  const {
    node: {
      model: { isExpanded },
    },
    virtualIndex,
  } = itemToToggle;
  itemToToggle.node.model.isExpanded = !isExpanded;
  triggerTreeRerender();
  setTimeout(() => {
    scrollTo(virtualIndex, { align: 'start' });
  });
};

export const expandItemModel = (modelToExpand) => {
  modelToExpand.isExpanded = true;
};

export const collapseItemModel = (modelToCollapse) => {
  modelToCollapse.isExpanded = false;
};

export const expandAll = (treeRoot, triggerTreeRerender) => {
  treeRoot.walk((node) => {
    if (!node.isRoot()) expandItemModel(node.model);
  });
  triggerTreeRerender();
};
export const collapseAll = (treeRoot, triggerTreeRerender) => {
  treeRoot.walk((node) => {
    if (!node.isRoot()) collapseItemModel(node.model);
  });
  triggerTreeRerender();
};
export const getToggleExpandShouldExpand = (treeRoot) => {
  let shouldExpand = false;
  treeRoot.walk((node) => {
    if (!node.isRoot()) {
      if (!node.model.isExpanded) {
        shouldExpand = true;
        // eslint-disable-next-line no-useless-return
        return;
      }
    }
  });
  return shouldExpand;
};

export const toggleExpandAllHelper = (
  isExpand,
  treeRoot,
  triggerTreeRerender,
  setLatestToggledItem,
) => {
  if (isExpand === true) expandAll(treeRoot, triggerTreeRerender);
  else if (isExpand === false) collapseAll(treeRoot, triggerTreeRerender);
  else {
    const shouldExpand = getToggleExpandShouldExpand(treeRoot);
    if (shouldExpand === true) expandAll(treeRoot, triggerTreeRerender);
    else collapseAll(treeRoot, triggerTreeRerender);
  }
  setLatestToggledItem(null);
};

export const useNotifyExpandedChange = (
  propsWithDefaults,
  { latestToggledItem, expandedGroups },
) => {
  const notifyExpandedChange = useCallback(
    (expandedGroupHashmap) =>
      propsWithDefaults.onExpandChange(expandedGroupHashmap, latestToggledItem),
    [propsWithDefaults, latestToggledItem],
  );

  useEffect(() => {
    notifyExpandedChange(expandedGroups);
  }, [expandedGroups]);
};
