/* eslint-disable max-lines */
import React, { useContext } from 'react';
import { SortableContainer } from '@elliemae/react-sortable-hoc';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import { DSCircularProgressIndicator } from '@elliemae/ds-basic/CircularProgressIndicator';

import TreeViewContext from '../TreeViewContext';
import { TreeListPropTypes } from '../prop-types';

import { TreeItem, DragAndDropTreeItem } from './TreeItem';
import {
  DSTreeViewPrefix,
  treeListBlockName,
} from '../config/cssClassesConstants';

export const treeListNoItemsBn = `${treeListBlockName}-no-items`;
export const TreeListNoItems = aggregatedClasses('div', {
  'data-testid': treeListNoItemsBn,
})(treeListNoItemsBn);

export const TreeListWrapper = aggregatedClasses('ul', {
  'data-testid': treeListBlockName,
  role: 'tree',
})(treeListBlockName, null, ({ rowSize }) => ({
  [`rowsize-${rowSize}`]: rowSize,
}));

const compactRowClass = `${DSTreeViewPrefix}-tv-row-size-compact`;
const normaRowClass = `${DSTreeViewPrefix}-tv-row-size-normal`;

export const TreeList = (props) => {
  const { onMouseDragOverItem } = props;
  const ctx = useContext(TreeViewContext);
  const {
    virtualListRef,
    virtualListHelpers,
    visibleItems,
    withDragAndDrop,
    props: { width, height, rowSize, noItemsPlaceholder, isLoading },
  } = ctx;
  const { totalSize, virtualItems } = virtualListHelpers;
  const className = `${
    rowSize === 'compact' ? `${compactRowClass}` : `${normaRowClass}`
  }`;

  return (
    <TreeListWrapper
      ref={virtualListRef}
      className={className}
      style={{
        height: `${typeof height === 'number' ? `${height}px` : height}`,
        width: `${typeof width === 'number' ? `${width}px` : width}`,
        overflow: 'auto',
      }}
    >
      {isLoading ? (
        <div
          style={{
            width: '100%',
            height: '100%',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <DSCircularProgressIndicator
            size="xl"
            loading={isLoading}
            showLabel
            waiting={false}
            showTooltip={false}
          />
        </div>
      ) : null}
      {!isLoading && virtualItems.length === 0 ? (
        <TreeListNoItems>{noItemsPlaceholder}</TreeListNoItems>
      ) : null}
      {!isLoading && virtualItems.length !== 0 ? (
        <div
          style={{
            height: `${totalSize}px`,
            width: '100%',
            position: 'relative',
          }}
        >
          {virtualItems.map((virtualItem) => {
            const { index, measureRef, start } = virtualItem;
            const item = visibleItems[index];
            item.virtualIndex = index;
            const { id } = item;

            const style = {
              position: 'absolute',
              top: 0,
              left: 0,
              width: '100%',
              transform: `translateY(${start}px)`,
            };

            const listItemProps = {
              key: `DS-TreeView-List-Item-${id}`,
              virtualItemRef: measureRef,
              index, // this is consumed by the DnD HOC
              itemIndex: index,
              item,
              itemWrapperStyle: style,
            };

            if (withDragAndDrop)
              return (
                <DragAndDropTreeItem
                  {...listItemProps}
                  onMouseDragOverItem={onMouseDragOverItem}
                />
              );
            return <TreeItem {...listItemProps} />;
          })}
        </div>
      ) : null}
    </TreeListWrapper>
  );
};
TreeList.propTypes = TreeListPropTypes;
export const DragAndDropTreeList = SortableContainer(TreeList);
