import React from 'react';
import { TreeViewItemPropType } from '../prop-types';
import {
  DSTreeViewPrefix,
  treeItemBlockName,
} from '../config/cssClassesConstants';

const className = `${DSTreeViewPrefix}-${treeItemBlockName}-children-count-displayer`;

export const ChildrenCountDisplayer = (props) => {
  const { item } = props;
  const { isGroup, children } = item;

  if (!isGroup) return null;
  // return ` | ${children.length} Items`;
  return (
    <span
      data-testid="tree-item-children-count-displayer"
      className={className}
    >{` | ${children.length} Items`}</span>
  );
};
ChildrenCountDisplayer.propTypes = { item: TreeViewItemPropType.isRequired };
