/* eslint-disable no-unused-vars */
import { createContext, createRef } from 'react';
// import Folder from '@elliemae/ds-icons/Folder';
// import FilePdf from '@elliemae/ds-icons/FilePdf';

export const defaultProps = {
  plugins: [],
  selection: {}, // { [id]:[state] } -> e.g. {1:true, 2:'mixed'}
  instanceRef: createRef(),
  disableGroups: false,
  disableLeafs: false,
  disableIcons: false,
  fluid: false,
  isMultiSelect: false,
  isSingleSelect: false,
  restrictDragAndDrop: false,
  showChildrenAmount: false,
  sortable: false,
  isLoading: false,
  onInstanceRefInitialized: (istanceRef) => {},
  onItemClick: (item, e) => {},
  onItemFocus: (params) => {}, // { itemIndex: number, scrollToItem: func }
  onVisibleItemsChange: (newVisibleItems) => {},
  onOrderChange: (newData, oldData) => {},
  onSelectionChange: (selectionHasmap, item, indexAndScrollTo) => {}, // indexAndScrollTo = { scrollToItem: ()=>{}, itemIndex :number }
  onExpandChange: (expandedHashmap, itemToggled) => {}, // expandedHashmap -> {[id]:true, ...[id]:true} itemToggled -> item that caused the change for being toggled
  rowSize: 'normal', // ['normal', 'compact']
  labelOverflow: 'wrap', // ['wrap', 'wrap-all', 'truncate']
  noItemsPlaceholder: 'No items found',
  nameKey: 'name', // use to retrieve the label text inside ever data item
  width: '100%',
  height: '100%',
  groupIcon: null, // <Folder />,
  itemIcon: null, // <FilePdf />,
  searchQuery: '',
};

export const defaultRelativeMouseCoord = {
  isBefore: false,
  isAfter: false,
  isValid: true,
  item: null,
  x: null,
  y: null,
  relativeElementDOMRect: null,
  relativeElementIndex: null,
};

/** Context for cross component communication */
const TreeViewContext = createContext({
  props: defaultProps,
  visibleItems: [],
  withRadioChecks: false,
  withCheckboxChecks: false,
  withDragAndDrop: false,
  relativeMouseCord: defaultRelativeMouseCoord,
  setRelativeMouseCord: () => {},
  labelRenderer: (value) => value,
  triggerTreeRerender: () => {},
  tree: {},
  virtualListHelpers: {},
  virtualListRef: {},
  treeRoot: {},
  isDragging: false,
  setIsDragging: () => {},
  isKeyboardDragging: false,
  setIsKeyboardDragging: () => {},
  draggedItem: null, // item
  setDraggedItem: () => {},
  uniqueTreeViewUUID: '',
  selectedItem: null, // item
  setSelectedItem: () => {},
  focusedItem: null, // item
  setFocusedItem: () => {},
  selectedCheckboxes: {}, // { [id]:[state] } -> e.g. {1:true, 2:'mixed'}
  setSelectedCheckboxes: () => {},
  expandedGroups: {}, // { [id]:true } -> e.g. {1:true, 2:true} // false are not listed
  setExpandedGroups: () => {},
  latestToggledItem: null, // item -> if toggle all -> should be undefined
  setLatestToggledItem: () => {},
  hoverItem: null, // item
  setHoverItem: () => {},
});

export default TreeViewContext;
