import React, { useContext } from 'react';
import { cx } from '@elliemae/ds-utilities/utils';

import TreeViewContext from '../TreeViewContext';

export const Icon = (props) => {
  const { item } = props;
  const isGroupOrHasChildrens =
    item.isGroup || (Array.isArray(item.children) && item.children.length > 0);
  const ctx = useContext(TreeViewContext);
  const {
    props: { groupIcon, itemIcon },
  } = ctx;
  const icon = isGroupOrHasChildrens ? groupIcon : itemIcon;
  if (!icon) return null;

  return React.cloneElement(icon, {
    'data-testid': isGroupOrHasChildrens
      ? 'tree-item-group-icon'
      : 'tree-item-icon',
    className: cx(icon.props.className, 'tree-item-icon'),
  });
};
